
<template>
    <div class="page home">
        <div class="books-detail-head">
        <div class="books-detail-imgshow"> <img :src="booksdetail.cover" alt=""></div>
        <p class="books-detail-title">{{booksdetail.name}}</p>
        <p class="author">{{booksdetail.author}}</p>
        <p class="price">¥{{booksdetail.price}}</p>
   

        </div>
    
    <div class="books-detail-buy">
      <p> <span class="shu">数量 </span> 已选<span class="num">1</span> 件</p> 
            <div class="numbox">
         <span @click="down" class="down icon">-</span> 
        <input  class="book-num icon" type="number" value="1"  readonly="readonly" >
         <span @click="add" class="add icon">+</span>
            </div>
            <span @click="gobuycar" class="get-buycar">购</span>
    </div>
    <div class="books-detail-des">
            <p class="des-title">图书详情</p>
        <div class="books-des">
            <p>{{booksdetail.describe}}</p>
        </div>
    
        
    </div>
    </div>
</template>

<script>
export default {
    name: 'Detail',
    data() {
        return {
            booksdetail:null,
            booksbuy:{
                usename:'',
                name:'',
                price:0,
                num:null,
                cover:''
            },
        
            ishas:false,
            islogin:false,
            loginusename:null,
            usemsg:[],
            booksbox:[],
      }
    },
    created() {
    
    
    

        //判断用户是否登录
          if(localStorage.getItem('islogin') == null){
            this.islogin = this.islogin;
        }
        else{
            this.islogin = JSON.parse(localStorage.islogin)
        }
        //判断登录用户是谁
         if(localStorage.getItem('loginusename') == null){
            this.loginusename = this.loginusename;
        }
        else{
            this.loginusename = JSON.parse(localStorage.loginusename)
        }
        //加载后台用户数据
         if(localStorage.getItem('usemsg') == null){
            this.loginusename = this.usemsg;
        }
        else{
            this.usemsg = JSON.parse(localStorage.usemsg)
        }
        
        //加载传入详情页的数据
        this.booksdetail = JSON.parse(localStorage.booksdetail);
     
    },


    methods:{
        //价格加减
        add(){
            this.$('.book-num').val(parseFloat(this.$('.book-num').val())+1);
            this.$('.num').text(this.$('.book-num').val());
        },
        //价格加减
        down(){
            if(this.$('.book-num').val()==0 || this.$('.book-num').val()==99){
                return;
            }
            this.$('.book-num').val(parseFloat(this.$('.book-num').val())-1);
             this.$('.num').text(this.$('.book-num').val());
        },



        //购物车功能
        gobuycar(){
             
            //判断是否登录
            if(!this.islogin){
                alert('添加购物车 请您先登录！');
                return;
            }
            else{ 
            //对当前用户的购物车进行操作
                var usebox = JSON.parse(localStorage.usemsg);
                var newusebox = null;
            
    this.$(usebox).each((i,m)=>{
                
                    //遍历购物车查询是否重名   
                   
                 
               if(this.loginusename == m.name)   
              { 
                    
                      this.booksbox = m.buycar

                    this.$(this.booksbox).each((i,n)=>{
                        if(n.name == this.$('.books-detail-title').text()){
                            this.ishas = true;
                        }
                    })
                    if(this.ishas){ 
                        alert('该书籍已经加入购物车！');
                        return;}
                    else{
                        this.booksbuy.usename = this.loginusename;
                        this.booksbuy.name = this.$('.books-detail-title').text();
                        this.booksbuy.num  = this.$('.book-num').val();
                        this.booksbuy.cover= this.booksdetail.cover;
                        this.booksbuy.price = this.booksdetail.price;
                        //书籍信息汇总到盒子里面
                        this.booksbox.push(this.booksbuy);
                            m.buycar = this.booksbox;
                            alert('添加购物车成功！')
                    }
                };
            })
            //将用户独有购物车数据存入后台总库
                        localStorage.usemsg = JSON.stringify(usebox);


             }
        }
    
    },
  
  
}
</script>

<style scoped>
.get-buycar{
    color:rgb(255, 255, 255);
    display: block;
    width: 40px;
    height: 40px;
    position: absolute;
    right: 20px;
    top: 50%;
    border-radius: 50%;
    font-size: 30px;
    line-height: 40px;
    background: red;
    cursor: pointer;
    transform: translateY(-50%);
}
.books-detail-head{
    width: 100%;
    padding: 10px;
    box-sizing: border-box;
    background: #fff;
}
.books-detail-imgshow{
    width: 100%;
}
.books-detail-imgshow img{
    width: 70%;
    margin-top:20px;
}
p{
    text-align: left;

}
.books-detail-title{
font-size: 18px;
font-weight: bold;

}
.author{
    font-size: 14px;
    color:rgb(151, 151, 151);
   margin-bottom:10px;
}
.price{
    color:red;
    font-weight: bold;
}

.books-detail-buy{
    margin-top:10px;
    width: 100%;
    background: #fff; 
    padding: 10px;
    position: relative;
    box-sizing: border-box;
    height: 50px;

 
}
.books-detail-buy p{
    font-size: 12px;
    font-weight: bold;
    color:rgb(151, 151, 151);
    position: absolute;
    top:50%;
    transform: translateY(-50%);
}
    

.shu{
    font-size: 16px;
    font-weight: bold;
    color:black;

}

.icon{
    width: 24px;
    height: 24px;
    cursor: pointer;
    display: block;
    float: left;
    text-align: center;
    border:1px solid #ddd;
    line-height: 24px;
}
.numbox{
    width: 80px;
    position: absolute;
    left:100px;
    top:50%;
    transform: translateY(-50%);
}
.books-des p{

    letter-spacing: 2px;
    border:4px dotted rgb(241, 62, 7);
    margin-bottom:50px;
    background: #fff;
    margin-top:10px;
}
.des-title{
    text-align: center;
    font-weight: bold;
    font-size: 20px;
    margin:10px;
}
</style>


