import Vue from 'vue'
import Router from 'vue-router'
import Home from '../pages/home'
import Mine from '../pages/mine'
import News from '../pages/books'
import Car from '../pages/car'
import Detail from '../pages/detail'
import Order from '../pages/order'
import Address from '../pages/address'
import Passbook from '../pages/passbook'
import Myorders from '../pages/myorders'




Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      component: Home,
      name: 'home',
      title:'图书首页'
    },
    {
      path: '/books',
      component: News,
      name: 'books',
      title:'书城'
    },
    {
      path: '/mine',
      component: Mine,
      name: 'mine',
      title:'我的'
    },
    {
      path:'/car',
      component:Car,
      name:'car',
      title:'购物车'

    },
    {
      path:'/detail',
      component:Detail,
      name:'detail',
      title:'图书详情'
    },
    {
      path:'/order',
      component:Order,
      name:'order',
      title:'订单结算'
    },
    {
      path:'/address',
      component:Address,
      name:'address',
      title:'收货地址'
    },
    {
      path:'/passbook',
      component:Passbook,
      name:'passbook',
      title:'订单详情'
    },
    {
      path:'/myorders',
      component:Myorders,
      name:'myorders',
      title:'我的'
    }
  ]
})



