<template>
    <div class="page home">
        <div class="addresbox">
            <ul v-if="hasaddress" class="address-list">
                <li  v-if="!addlocal"  v-for="(adr,index) in useaddress" :key="index">
                    <span  class="adrnum">地址{{index+1}}</span>

                    <div :class="['adrbox',{'chosed':!index}]">
                        <p class="adr-pro">省份：{{adr.prove}}</p>
                        <p class="adr-cit">城市：{{adr.city}}</p>
                        <p class="adr-road">具体街道：{{adr.road}}</p>
                    </div>
                    <div @click="defaultlocal" class="cover"></div>
                </li>
            </ul>

            <div v-if="nogoods" class="noaddress">
                <h1>暂无收获地址，请设置！</h1>
            </div>

            <div v-if="addlocal" class="newa-ddress" >
                <input class="proves" type="text" placeholder="请输入省份">
                <input class="city" type="text" placeholder="请输入城市">
                <input class="road" type="text" placeholder="请输入街道">
                <button @click="getaddress" class="sure-btn">确认添加</button>

            </div>
        <button @click="addlocaltion" class="add-address">添加收获地址</button>
        </div>
    </div>
</template>
<script>
    export default({
        name:"Address",
      data() {
        return {
      useaddress:[],
      hasaddress:false,
      loginusename:null,
      usemsg:null,
      addlocal:false,
      nogoods:true,
      }
    },
    created() {
            //匹配当前用户
              this.loginusename = JSON.parse(localStorage.loginusename);
              this.usemsg = JSON.parse(localStorage.usemsg);
              this.$(this.usemsg).each((i,m)=>{
                  if(this.loginusename == m.name){
                  this.useaddress = m.address;
                   if(this.useaddress == ''||this.useaddress == []){
                    this.hasaddress=false;
                   }
                   else{
                        this.hasaddress = true;
                        this.nogoods = false;
                   }
                  }
              })
          
                
           
            
    },
    methods:{
        //打开地址添加栏
        addlocaltion(){
            this.addlocal = !this.addlocal;
            if( this.useaddress != "" ||this.useaddress != []){
                this.nogoods = false;
            }
        },
        //获取地址
        getaddress(){
            var address = {
                prove:'',
                city:'',
                road:""
            }
            address.prove = this.$('.proves').val();
            address.city = this.$('.city').val();
            address.road = this.$('.road').val();

            if(this.$('.proves').val() != ''&&this.$('.city').val()!=''&&this.$('.road').val()!=''){
                this.$(this.usemsg).each((i,m)=>{
                            if(this.loginusename == m.name){
                                m.address.push(address);
                                this.useaddress = m.address;
                                this.hasaddress = true;
                            }
                        })
                        alert('添加成功！');
                        localStorage.usemsg = JSON.stringify(this.usemsg);
                        this.addlocal = !this.addlocal;    
            }
            else{
                alert('请完善地址信息！')
            }

        },
        defaultlocal(e){
          
            //所有地址栏移除样式
            this.$('.adrbox').removeClass('chosed');
            //当前选中地址栏增加样式
            this.$(e.path[1]).find('.adrbox').addClass('chosed')

          
        }
    }

    })
</script>

<style scoped="">
.cover{
    position: absolute;
    width: 100%;
    height: 100%;
    cursor: pointer;
    z-index: 100;
    top:0;
    left: 0;
    box-sizing: border-box;
    
}
.adrbox{
    width: 100%;
    height: 100%;
    box-sizing: border-box;
 
}
.adrnum{
    position: absolute;
    left:4px;
    top:10px;
}
.chosed{
    border: 1px dotted red ;
}
.address-list{
    width: 100%;
    overflow: hidden;
}
.address-list li {
    box-sizing: border-box;
    position: relative;
    width: 100%;
    background: #fff;
    margin-top:8px;
    height: 100px;
    overflow: hidden;
    
}
.address-list li p{
    text-align: left;
    margin-left:100px;
    letter-spacing: 4px;
}
.newa-ddress{
    width: 100%;
    position: absolute;
    top:50px;
}
.adr-pro{
    margin-top:10px;
    font-size: 20px;
    font-weight: bold;
}
.newa-ddress input{
    width: 200px;
    height: 30px;
    display: block;
    margin: 10px auto;
    line-height: 30px;
    letter-spacing:1px;
    padding-left:10px;
}
.addresbox{
    width: 100%;
    height: 100%;
    position: relative;
}
.sure-btn{
    width: 100px;
    height: 50px;
    background: rgba(43, 95, 0, 0.61);
    outline: none;
    color: #fff;
    outline: none;
    cursor: pointer;
    font-size: 12px;
    border:none;
    border-radius: 10px;
}
.add-address{
    width: 100px;
    height: 50px;
    background: rgb(255, 20, 59);
    outline: none;
    color: #fff;
    outline: none;
    cursor: pointer;
    font-size: 12px;
    border:none;
    border-radius: 10px;
    position: fixed;
    bottom:60px;
    left:10px;
}
.noaddress{
    position: fixed;
    width: 100%;    
    left:50%;
    top:50%;
    transform: translateX(-50%) translateY(-50%);
}
</style>



