<template>
    <div class="page home">
        <div class="show-use-order">
            <p class="usename ues-title">
                尊敬的用户{{loginusename}}
            </p>
            <p class="ues-title">您的订单详情</p>


<ul class="use-order-list">
    <li class="every-order-show">
            <ul class="order-li">
                <li v-for="(order,index) in showorder" :key="index">
                    <div class="order-box">
                        <img :src=order.cover alt="">
                        <p class="name">书名：{{order.name}}</p>
                        <p class="count">总数：{{order.chosenum}}</p>
                    </div>
                </li>
            </ul>
               </li>
</ul>
            <p class="alltotal">总价：{{total}}元</p>
            <p class="getlocation">
                收获地址：
                <span>{{localtion.prove}}</span>
                <span>{{localtion.city}}</span>
                <span>{{localtion.road}}</span>

            </p>
        </div>
    </div>
</template>
<script>
    export default({
        name:'Sureorder',
        data(){
            return{
            loginusename:null,
            showorder:null,
            total:0,
            localtion:null
            }
        },
        created() {
            //匹配当前用户
               this.loginusename = JSON.parse(localStorage.loginusename);
               this.showorder = JSON.parse(localStorage.payedorder).orderbooks;
               this.localtion = JSON.parse(localStorage.payedorder).location;
               this.$(this.showorder).each((i,m)=>{
                   this.total = this.total + m.total  
               })
               this.total = this.total.toFixed(2)
              
        },
    })
</script>
<style>
.getlocation{
    text-align: center;
    margin-top:10px;
    width: 100%;
    background: rgb(4, 92, 44);
    height: 70px;
    color:#fff;
    font-size: 20px;
    line-height: 70px;
    letter-spacing: 2px;
    margin-bottom:50px;
}
.alltotal{
    display: block;
    height: 40px;
    width: 100%;
    margin-top:10px;
    line-height: 40px;
    text-align: center;
    background: #fff;
    color:red;
    font-size: 24px;
    font-weight: bold;
    
}
.show-use-order{
    width: 100%;
    text-align: left;
}
img{
    width: 20%;

}
.order-box p {
    width: 240px;
    position: absolute;
    left: 100px;
    font-size: 14px;
    color:rgb(41, 51, 3);
}
.name{
    top:10px;
    font-weight: bold;
}
.count{
    top:70px;
}

.ues-title{
    text-align: center;
    font-weight: bold;
    margin-top:10px;
}
.order-li{
    width: 100%;
}
.order-li li{
    width: 100%;
    padding: 10px;
    box-sizing: border-box;
    height: 120px;
    background: #fff;
    margin-top:10px;
    position: relative;
}

</style>



