<template>
  <div id="app">
    <div class="navigationBar">
      <h1 class="title">{{this.$store.state.title}}</h1>
    </div>
    <!-- 视图 -->
    <router-view ></router-view>
    <!-- tabBar -->
    <ul class="tabBar">
      <li class="tabBarItem" v-for="(item, index) in links" :key="index">
        <router-link :to="{path:item.path,query:{title:item.title}}">{{item.title}}</router-link>
      </li>
    </ul>
  
  </div>
</template>

<script>




export default {
  name: 'App',
  data(){
    return{
      books:null,
      links: [
        {
          title: "首页",
          path: "/",
          
        },
        {
          title: "书城",
          path: "/books"
        },
        {
          title: "购物车",
          path: "/car"
        },
        {
          title:'我的',
          path:'/mine'
        }
      ]

    }
  
  },
  created() {
   
    //判断是否登录
    if(localStorage.getItem('islogin') == null){
      localStorage.islogin = JSON.stringify(false)
    }
            
        },
     
  
}
</script>

<style>
/* 首页样式 */
.hidden {
  display: none;
}
*{
  margin: 0;padding: 0;}
html,body{
  height: 100%;
  background: rgb(243, 243, 243);}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  height: 100%;
}
ul{
  list-style: none;
}
.navigationBar {
  height: 64px;
  background: rgb(45, 71, 45);
  color: #fff;
  width: 100%;
  position: fixed;
  top:0;
  z-index: 1000;
  margin-bottom: 64px;
}
.navigationBar > .title {
  line-height: 64px;
  text-align: center;
  font-size: 16px;
}
.page {
  margin-top:64px;
  height: calc(100% - 49px - 64px);
  transition: .5s;
 
}
.tabBar {
  list-style: none;
  height: 49px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  position: fixed;
  bottom:0;
  z-index: 100000;
}
.tabBarItem {
  width: calc(100% / 4);
  height: 100%;
}
.tabBarItem  a {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 12px;
  width: 100%;
  height: 100%;
  background:rgb(45, 71, 45);
  color: #fff;
  text-decoration: none;
}
.tabBarItem  a.selected {
  background: purple;
}

</style>


