
<template>
    <div class="page home">
        <!-- 若有书籍 -->
        <ul class="booksshow">
        <li v-if="booksbuyshow" class="bookbuyli" v-for="(m,index) in booksbuyshow" :key="index" :data-set="index">
            
            <img class="bookimg" :src=m.cover alt="">
            <div class="booksmsg">
            <p class="title">{{m.name}}</p>
            <p class="total">¥{{m.price}}</p>
            <p class="price">¥{{m.price}}</p>
            </div>
      
            <div class="numbox">
         <span @click="down" class="down icon">-</span> 
        <span  class="book-num icon" type="number" readonly="readonly" >1</span>
         <span @click="add" class="add icon">+</span>
            </div>
                  <p @click="del" class="delete">删</p>
                  
                <input class="check"  type="checkbox" name="" id="">
            </li>
        </ul>
        <!-- 若没有书籍 -->
       <div v-if="!booksbuyshow" class="nobooks">
           <p >无商品</p>
           <span>嘤嘤嘤</span>
           <span @click="gobookshop" class="gobooks">快来书城看看吧</span>
       </div>
    
        <!-- 订单结算按钮 -->
        <div v-if="booksbuyshow"   class="order-box">
            <button  @click="ordershow" type="button" class="order-btn ">结算</button>
        </div>

        

    </div>
</template>

<script>
import router from '../router'
export default {
    name: 'Car',
    data() {
        return {
      loginusename:null,
      booksbuyshow:null,
      usemsg:null,
      whichuser:null
      }
    },
    created() {

              //传递当前用户id
              //展示不同用户的独特购物车
            if(JSON.parse(localStorage.islogin)){
                this.loginusename = JSON.parse(localStorage.loginusename);
                this.usemsg = JSON.parse(localStorage.usemsg);
                this.$(this.usemsg).each((i,m)=>{
                   if(this.loginusename == m.name){
                   this.booksbuyshow = m.buycar;
                   this.whichuser = i;
                   if(this.booksbuyshow == '' || this.booksbuyshow == null){
                    this.booksbuyshow=null
                   }
                  }
              })
            }
            else{
            this.booksbuyshow = null;
            localStorage.orderbooks = JSON.stringify([])
            }

           
            
    },
    methods:{
        //加数
        add(e){
            this.$(e).each((i,m)=>{
                var idx = (m.path[2].dataset.set);
            this.$(this.$('.book-num')[idx]).text(parseFloat(this.$(this.$('.book-num')[idx]).text())+1);
            this.$(this.$('.total')[idx]).text('¥'+(this.$(this.$('.book-num')[idx]).text() * parseFloat(this.$(this.$('.price')[idx]).text().slice(1))).toFixed(1));
            
                })
        },

        //减数
        down(e){
          
                var ishas = false;
                this.$(e).each((i,m)=>{
                var idx = (m.path[2].dataset.set);
                 if(this.$(this.$('.book-num')[idx]).text()==1 || this.$(this.$('.book-num')[idx]).text()==99){
                        ishas = true;
                }
                if(ishas){
                    return;
                }
                else{
                 this.$(this.$('.book-num')[idx]).text(parseFloat(this.$(this.$('.book-num')[idx]).text())-1);
                 this.$(this.$('.total')[idx]).text('¥'+(this.$(this.$('.book-num')[idx]).text() * parseFloat(this.$(this.$('.price')[idx]).text().slice(1))).toFixed(1));
                }
                })
        },
        //删除

        del(e){
            //获取终极购物车数据
            this.$(e).each((i,m)=>{
            var idx = (m.path[1].dataset.set);
            var a = confirm('确定删除？')
            if(a){
                this.$(this.usemsg).each((i,m)=>{   
                if(this.loginusename == m.name){
                    m.buycar.splice(idx,1);
                    this.booksbuyshow = m.buycar;
                }
                })   
                localStorage.usemsg = JSON.stringify(this.usemsg);
            }  
            });      
            //刷新页面
            if(this.booksbuyshow == '' || this.booksbuyshow == null){
                    this.booksbuyshow=null;
                   }
        },

        //订单传递
        ordershow(){
           var box = []
           var isok = false;
            this.$('.check').each((i,m)=>{
                if(m.checked){
                    this.usemsg[this.whichuser].buycar[i].chosenum= this.$(m).parent().find('.book-num').text()
                    this.usemsg[this.whichuser].buycar[i].total=  parseFloat(this.$(m).parent().find('.total').text().slice(1))
                    box.push(this.usemsg[this.whichuser].buycar[i]);
                    isok = true;
                }
                
            })
            if(isok){
                localStorage.orderbooks = JSON.stringify(box);
                this.$router.push({path:'../order',query:{title:'订单结算'}})
            }
            else{
                alert('请选择您要结算的商品！')
            }
           
        },

        //打开书城
        gobookshop(e){
            this.$router.push({path:'/books',query:{title:'书城'}})
        }
     
    }
}
</script>

<style scoped="">
.check{
    position: absolute;
    display: block;
    top:10px;
    left:20px;
    outline: none;
}
.order-show{
    text-align: center;
    width: 80%;
    height: 200px;
    background: #ddd;
    position: fixed;
    top:50%;
    left:50%;
    transform: translateX(-50%) translateY(-50%);
}
.order-box{
    width: 50px;
    height: 50px;
    position: fixed;
    bottom:80px;
   
    border-radius: 10px;
}
.order-box .order-btn{
    border-radius: 10px;
    color:#fff;
    outline: none;
    border: none;
    line-height: 40px;
    font-size: 16px;
    width: 100%;
    height: 100%;
    letter-spacing: 5px;
    background: rgb(245, 89, 89);
    cursor: pointer;
  
}
.gobooks{
    color:rgb(236, 98, 5);
    font-weight: bold;

}
.nobooks {
    text-align: center;
    width: 100%;

}
.nobooks p{
    font-size: 40px;
    height: 40px;
    margin-top:200px;
}
.nobooks span{
    margin-top:20px;
    display: block;
}
.booksshow{
    width: 100%;
    text-align: left;
    margin-bottom: 50px;
 
}
.booksshow li{
    width: 100%;
    padding: 10px;
    background: #fff;
    margin-top:10px;
    height: 160px;
    position: relative;
    box-sizing: border-box;
   
}

.icon{
    width: 24px;
    height: 24px;
    cursor: pointer;
    display: block;
    float: left;
    text-align: center;
    border:1px solid #ddd;
    line-height: 24px;
}
.delete{
    position: absolute;
    top: 70px;
    display: block;
    right:10px;
    cursor: pointer;
    color:#fff;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    text-align: center;
    font-weight: bold;
    line-height: 30px;
    background: red;
}
.numbox{
    width: 80px;
    position: absolute;
    right: 10px;
    bottom:28px;
  
}
.booksshow li img{
    width: 24%;

    display: block;
    margin-top:20px;
    margin-left:10px;
}
.booksmsg{
    width: 100%;
    position: relative;
    padding: 10px;
    box-sizing: border-box;
}
.title{
    font-size: 14px;
    position: absolute;
    font-weight: bold;
    bottom:110px;
    left:33%;
}
.price{
    width: 100px;
    color: #ddd;;
    font-weight: bold;
    font-size: 16px;
    position: absolute;
    bottom:70px;
    left:33%;
}
.total{
    width: 100px;
    color: red;
    font-weight: bold;
    font-size: 20px;
    position: absolute;
    bottom:20px;
    left:33%;
}
</style>


