import Vue from 'vue';
import Vuex from 'vuex';
Vue.use(Vuex);

export default new Vuex.Store({

    //数据
    state:{
        title:"图书首页",
        islogin:false,
        books:null,
    },
    //修改数据
    mutations:{
        changetitle(state,title){
            state.title = title;
        },
        changelogin(state,islogin){
            state.islogin = islogin
        }
        
    }

})


