
<template>
    <div class="page home">
        
        <!-- 已经登陆 -->
        <div v-if="islogin" class="logined">
                    <header class="header">
                    <div class="use-head">
                        <img src="../images/1.jpeg" class="use-img" alt="">
                        </div>
                        <p class="use-name">{{usename}}</p> 
                    </header>
                    <main>
                        <ul class="mine-list">
                            <li  @click="openmyorder" class="mine-li" >
                                <p>我的订单</p>
                            </li>
                               <li @click="openaddrespage" class="mine-li" >
                                <p>我的收货地址</p>
                            </li>
                            <li class="mine-li">
                                <p>联系客服</p>
                                </li>
                           
                        </ul>
                    </main>
                    <button @click="outin" class="outin" type="button">注销</button>
        </div>
        <!-- 没有登录 -->
        <div v-else class="unlogin">

            <!-- 登录注册信息切换 -->
            <div v-if="!istoreginst" class="login-iptbox">
                <input  type="text" class="usename-login login lpipt"  maxlength="11" placeholder="请输入手机号">
                <input  type="password" class="password-login password lpipt" maxlength="11" placeholder="请输入密码">
            </div>

            <div v-else class="regist-iptbox">
                <input  type="text" class="usename-regist  login lpipt"  maxlength="11" placeholder="请输入手机号">
                <input  type="password" class="password-regist password lpipt" maxlength="11" placeholder="请输入密码">
            </div>

            
            <!-- 登录注册按钮切换    -->
            <button  @click="login" v-if="!istoreginst" class="btn-login btn" type="button">登录</button>
            <button @click="regist" v-else class="btn-reginst btn" type="button">注册</button>

            <!-- 登录注册切换 -->
            <p @click="toreginst"  v-if="!istoreginst"  class="toreginst log">没有账号？点我注册</p>
            <p @click="toreginst" v-else  class="tologin log">返回登录</p>

        </div>
    
    </div>
</template>

<script>
import router from '../router'
export default {
    name: 'Mine',
    data() {
        return {
           
            islogin:false,
            istoreginst:false,
            usename:null
        }
    },
    created() {
        //判断是否登录
        if(localStorage.getItem('islogin') == null){
            this.islogin = this.islogin;
        }
        else{
            this.islogin = JSON.parse(localStorage.islogin)
        };
        
        //判断当前登录用户手机号码
         if(localStorage.getItem('loginusename') == null){
            this.usename = this.usename;
        }
        else{
            this.usename = JSON.parse(localStorage.loginusename)
        }
     
    },
    methods:{
        //跳转注册/登录
        toreginst(){
            this.istoreginst = !this.istoreginst;
            this.$('.unlogin input').val('');
        },
        //登录
        login(){
             var usebox = {
                name:'',
                password:'',
                buycar:[]
                         };
            var msgbox = [];
                    usebox.name = this.$('.usename-login').val();
                    usebox.password = this.$('.password-login').val();
            var isok = false;
                if(localStorage.getItem('usemsg') == null){
                            msgbox = msgbox;
                        }
                else{
                    msgbox = JSON.parse(localStorage.usemsg)
                    }
                this.$(msgbox).each((i,m)=>{
                    if(usebox.name == m.name && usebox.password ==m.password){
                       isok = true;
                    }
                    })
                    if(isok){
                        alert('登录成功！即将跳转首页...');
                        localStorage.islogin = JSON.stringify(isok);
                        this.$router.push({path:'/'});
                        localStorage.loginusename = JSON.stringify(usebox.name);

                    }
                    else{
                        alert('手机号码未注册或者密码有误！')
                    }
                    

        },
        //注册
        regist(){
            var usebox = {
                name:'',
                password:'',
                buycar:[],
                address:[],
                ordersdes:[]
                         };
            var msgbox = [];
            usebox.name = this.$('.usename-regist').val();
            usebox.password = this.$('.password-regist').val();
            var isok = false;
             if(usebox.name.length == 11 && usebox.password.length >= 6){

                    if(localStorage.getItem('usemsg')==null||JSON.parse(localStorage.usemsg)==''){
                        msgbox.push(usebox);
                        localStorage.usemsg = JSON.stringify(msgbox);
                        alert('注册成功！即将跳转首页...');
                        isok = true;
                        localStorage.islogin = JSON.stringify(isok);
                        this.$router.push({path:'/'})
                      
                        localStorage.loginusename = JSON.stringify(usebox.name);
                    }
                    else{
                        var hasreg = false;
                        msgbox = JSON.parse(localStorage.usemsg)
                        this.$(msgbox).each((i,m)=>{
                        if(usebox.name == m.name){
                        hasreg = true;
                            } })
                        if(hasreg){
                            alert('该用户已经注册！')
                            return
                            }
                        else{
                            msgbox.push(usebox);
                            localStorage.usemsg = JSON.stringify(msgbox);
                            alert('注册成功！即将跳转首页...');
                            isok = true;
                            localStorage.islogin = JSON.stringify(isok);
                            this.$router.push({path:'/'})
                            localStorage.loginusename = JSON.stringify(usebox.name)
                        }
                 }
            }
                    else{
                            alert('请输入正确格式的手机号/密码最低6位')
                
                        }

        },
        // 注销
        outin(){
            var a = confirm('确定退出吗？')
            if(a){
             localStorage.islogin = false;
             localStorage.loginusename = null;
              location.reload();
             
            }
         
        },
        //打开收获地址
        openaddrespage(){
            this.$router.push({path:'../address',query:{title:'收货地址'}})
        },
        openmyorder(){
            this.$router.push({path:'../myorders',query:{title:'我的订单'}})

        }
    }
}
</script>

<style scoped>
.outin{
    width: 80%;
    border-radius: 15px;
    background: rgba(235, 56, 56, 0.664);
    height: 48px;
    outline: none;
    font-weight: bold;
    color:#fff;
    font-size: 30px;
    border:none;
    letter-spacing: 5px;
    cursor: pointer;
    position: absolute;
    bottom: 100px;
    left:50%;
    transform: translateX(-50%);
}
.hidden{display: none;}
.unlogin{
    width: 100%;
    height: 500px;
    position: relative;
}
.lpipt{
    width: 80%;
    float: left;
    margin:20px auto;
    height: 30px;
    padding: 10px;
    letter-spacing: 2px;
    border: none;
    outline: none;
    display: block;
    position: absolute; 
}
.login{
    left: 50%;
    transform: translateX(-50%);
    top:130px;
}
.password{
    left: 50%;
    transform: translateX(-50%);
    top:200px;
}

.btn{
    color:#fff;
    background: orange;
    width: 80%;
    font-size: 20px;
    font-weight: bold;
    letter-spacing: 10px;
    border-radius: 4px;
    height: 54px;
    margin:0 auto;
    border:none;
    outline: none;
    cursor: pointer;
    position: absolute;
    top:360px;
    left:50%;
    transform: translateX(-50%);
}
.log{
    position: absolute;
    top:300px;
    left:50%;
    transform: translateX(-50%);
    cursor: pointer;
}
.header{
    position: relative;
    width: 100%;
    height: 200px;
    box-shadow: 0 0 30px rgb(230, 66, 94) inset;
    background: rgb(128, 50, 63);
}

.use-head{
    width: 100%;
}
.use-img{
    display: block;
    width: 100px;
    height: 100px;
    border-radius: 50%;
    position: absolute;
    top: 30px;
    left: 50%;
    transform: translateX(-50%);
}
.use-name{
    position: absolute;
    top: 140px;
    left: 50%;
    transform: translateX(-50%);
    font-size: 24px;
    font-weight: bold;
    color:#fff;
    text-shadow: 0 0 20px;
}
.mine-list{
    width: 100%;
    margin-top:10px;
    overflow: hidden;
}
.mine-li{
    background: #fff;
    height: 24px;
    line-height: 24px;
    text-align: left;
    padding: 8px;
    width: 100%;
    float: left;
    cursor: pointer;
    border-bottom:1px solid #ddd;
    margin-top:4px;
}

a{
    text-decoration: none;
    color:black;
}
</style>


