<template>
    <div class="page home">
        <!-- 判断该用户是否有订单 -->
            <div v-if="!hasorder" class="nogoods">无订单</div>
            <ul v-else class="use-orders-show">
                <li  v-for="(ord,i) in orders " :key="i" class="orders-li" :data-set="i">
                <div  @click="tothisorder" class="cover"></div>
                <p class="order-num">订单号码：1000{{i}}</p>
                    <ul class="one-order-show">
                        <li v-for="(book,n) in ord.orderbooks" :key="n" class="books-msg">
                                <img :src=book.cover alt="">
                                <div class="p-box">
                                <p >书名：{{book.name}}</p>  
                                <p >购买数量：{{book.chosenum}}</p> 
                                <p >书籍总价：{{book.total}}元 </p> 
                                </div>  
                        </li>
                    </ul>
        <!-- 展示该用户当前订单的收获地址选择 -->
                <div class="getlocal">
                    <h3>收货地址</h3>
                    <span>{{ord.location.prove}}省{{ord.location.city}}市{{ord.location.road}}街</span>
                </div>

               
                </li>
            </ul>
     

    </div>
</template>
<script>
    export default({
        name:'Myorders',
        data(){
            return{
                books:null,
                orders:null,
                usemsg:null,
                hasorder:false,
                loginusename:null
            }
        },
        created() {
            //匹配当前用户
              this.loginusename = JSON.parse(localStorage.loginusename);
              this.usemsg = JSON.parse(localStorage.usemsg);
              this.$(this.usemsg).each((i,m)=>{
                  if(this.loginusename == m.name){
                  this.orders = m.ordersdes;
                   if(this.orders == ''||this.orders == []){
                    this.hasorder=false;
                   }
                   else{
                        this.hasorder = true;
                   }
                  }
              })
              this.books = this.orders.orderbooks
          
        },
        methods:{
            //传递当前用户的订单信息
            tothisorder(e){
             localStorage.payedorder = JSON.stringify(this.orders[this.$(e.path[1])[0].dataset.set])
            this.$router.push({path:'../passbook',query:{title:'订单详情'}})
         
            }
        }


    })
</script>

<style>
.cover{
    width: 100%;
    height: 100%;
    position: absolute;
    top:0;
    left:0;
    z-index: 100;
}
.nogoods{
    width: 100%;
    text-align: center;
    position: fixed;
    top:50%;
    left:50%;
    font-size: 40px;
    transform: translateX(-50%) translateY(-50%);
}
.getlocal{
    margin-top:10px;
}
.use-orders-show{
    width: 100%;
    margin-bottom: 100px;
}
.books-msg{
    font-size: 14px;
    margin-top:20px; 
    position: relative;
}
.books-msg img{
    width: 20%;
    height: 100px;
}
.books-msg .p-box{
    position: absolute;
    left: 100px;
    bottom:10px;
}
.p-box p{
    white-space: nowrap;
    overflow: hidden;
    text-overflow:ellipsis;
    width: 200px;
    margin:10px;
}
.orders-li{
    padding: 8px;
    height: 100%;
    box-sizing: border-box;
    width: 100%;
    text-align: left;
    background: #fff;
    margin-top:10px;
    position: relative;
}

</style>



