
<template>
    <div class="page home">
        <!-- 搜索 -->
        <button type="button" class="search-btn-box"><input type="text" placeholder="请输入图书关键词"></button>
        <div class="lishowchange">变</div>
<main class="main">
    <!-- 书籍展示 -->
    <ul class="books-list">
        <li class="books-lishow-one " v-for="(m,index) in books " :key="index" :data-set="index"> 
             <img style="width:100px; height:120px" :src=m.cover alt="">
                <p class="books-name one">{{m.name}}</p>
                <p class="books-prcie one">¥{{m.price}}</p>   
                <p class="books-index" style="display:none">{{index}}</p>
                <div @click="passbook"  class="chose-books-cover"></div>
        </li>
    </ul>

</main>
    </div>
</template>

<script>
import router from '../router';
export default {
    name: 'Books',
    data() {
        return {
            books:null
           }
        },
        created() {
            this.books = JSON.parse(localStorage.books);
        },
        methods:{
            passbook(e){
                   this.$(e).each((i,m)=>{
                       var idx = (m.path[1].dataset.set);
                       localStorage.booksdetail = JSON.stringify(this.books[idx])
                   });
                   this.$router.push({path:'/detail',query:{title:'图书详情'}})
                   
               }
            }
        }


</script>

<style scoped>
.chose-books-cover{
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 10;
    opacity: 0;
    top:0;
    left:0;
    background: blue;
}
main{
    background: #fff;
}
.page{
    text-align: left;
}
.lishowchange{
    position: absolute;
    right:10px;
    top:74px;
    cursor: pointer;
    width: 30px;
    border-radius: 50%;
    text-align: center;
    line-height: 30px;
    height: 30px;
    background: #ddd;
}


.search-btn-box{
    margin-left:4px;
    margin-top:10px;
    width: 85%;
    padding: 3px;
    height: 30px;
    border-radius: 15px;
    outline: none;
    position: relative;
    border:none;
}
.search-btn-box input{
    border: none;
    outline: none;
    position: absolute;
    left:30px;
    top: 50%;
    transform: translateY(-50%);
    height: 28px;
    line-height: 28px;
    background: none;
}

.books-list{
    width: 100%;
    margin-bottom:50px;
}
.books-name {
    width: 220px;
    float: left;
    margin-left:25px;
    font-weight: bold;
    margin-top:10px;
    border-bottom:1px solid #ddd;
    height: 120px;

}

.books-lishow-one{
    width: 100%;
    padding: 4px;
    background: #fff;
    box-sizing: border-box;
    height: 140px;
    margin-top:10px;
    cursor: pointer;
    position: relative;

}
.books-lishow-one img{
    float: left;
}
.books-prcie{
    color: red;
    font-weight: bold;
    position: absolute;
    bottom:20px;
    left:130px;
}
</style>


