<template>
    <div class="page home">

        
        <ul class="order-show">
            <li  v-for=" (books,index) in orderbooks" :key="index">
                        <p class="name">{{books.name}}</p>
                        <p class="total">价格：¥{{books.total}}</p>
                        <p class="num">数量：{{books.chosenum}}</p>
                        <img class="orderbookimg" :src=books.cover alt="">
            </li>
        </ul>


            <div class="order-doing">
               
                <button @click="sureOrder" class="order-sure-btn btn">确认订单</button>
                <button @click="changeaddres" class="order-local-btn btn">选择地址</button>
            </div>

         <div class="local-box">
             <p class="local-title">收获地址：</p>
                <div class="locbox">
                 <p>省份：{{sureaddress.prove}}</p>
                 <p>市区：{{sureaddress.city}}</p>
                 <p>街道：{{sureaddress.road}}</p>
                </div>
        
         </div>

         <div v-if="changeing" class="changelocal">
               <ul  class="address-list">
                <li   v-for="(adr,index) in address" :key="index" :data-set="index">
                    <span  class="adrnum">地址{{index+1}}</span>
                   
                    <div class="adrbox">
                        <p class="adr-pro">省份：{{adr.prove}}</p>
                        <p class="adr-cit">城市：{{adr.city}}</p>
                        <p class="adr-road">具体街道：{{adr.road}}</p>
                    </div>
                    <div @click="defaultlocal" class="cover"></div>
                </li>
            </ul>
         </div>
            


    </div>
</template>

<script>
    export default({
        name:'Order',
        data(){
            return{
                orderbooks:null,
                sureaddress:null,
                loginusename:null,
                usemsg:null,
                changeing:false,
                address:null,
                isok:true
            }
        },
        created() {
            //匹配当前用户即将传递的订单信息
            this.orderbooks = JSON.parse(localStorage.orderbooks);
            this.loginusename = JSON.parse(localStorage.loginusename);
              this.usemsg = JSON.parse(localStorage.usemsg);
              this.$(this.usemsg).each((i,m)=>{
                  if(this.loginusename == m.name){
                      //这里获取到地址，后面添加地址可以直接使用此属性值
                   this.address = m.address;
                   if(this.address == ''||this.address == []){
                    this.address=[
                    {prove:'',
                    city:'',
                    road:''}]
                   }
                  }
              })
            //获取用户默认收货地址
              if(this.sureaddress == null){
                  this.sureaddress = this.address[0]
              }
             
        
        },


        methods:{
            //确定订单
            sureOrder(){
                var total = null;
                this.$('.total').each((i,m)=>{
                total += parseFloat(this.$(m).text().slice(4))
                })
                var ordermsg = {
                    orderbooks:[],
                    location:{}
                }
                ordermsg.orderbooks = this.orderbooks;
                ordermsg.location = this.sureaddress;
                //异常处理
                if(ordermsg.location.prove == '' ){
                    alert('请完善收货地址信息！');
                    
                }
               
                else{   
                    console.log(ordermsg.location)
                this.$router.push({path:'../passbook',query:{title:'订单详情'}})

                this.$(this.usemsg).each((i,m)=>{
                                    if(this.loginusename == m.name){
                                        m.ordersdes.push(ordermsg)
                                    }
                                })

                 localStorage.payedorder = JSON.stringify(ordermsg)
                 localStorage.usemsg = JSON.stringify(this.usemsg)}
              
            },
            //选择地址
            changeaddres(){
                if(this.sureaddress.prove ==''){
                    var a =  confirm('您并未设置收货地址，是否前往设置？')
                   if(a){
                       this.$router.push({path:'../address',query:{title:'收货地址'}})
                   }
                    
                }
                else{this.changeing=!this.changeing}
                
            },

            //获取用户收货地址
            defaultlocal(e){
          
            //所有地址栏移除样式
            this.$('.adrbox').removeClass('chosed')
            //当前选中地址栏增加样式
            this.$(e.path[2]).find('.adrbox').addClass('chosed');
         
            this.changeing = !this.changeing

            //选中的用户的地址下标
           
            this.sureaddress = this.address[this.$(e.path[1])[0].dataset.set]
    
        }
        }
    })
</script>
<style >
.cover{
    width: 100%;
    position: absolute;
    height: 100%;
    top:0;
    left:0;
    z-index: 100;
}
.changelocal{
  
    margin-top:-100px;
    margin-bottom:300px;
    width: 100%;
}
.adrbox{
    width: 100%;
    height: 100%;
    box-sizing: border-box;
 
}
.adrnum{
    position: absolute;
    left:4px;
    top:10px;
}
.chosed{
    border: 1px dotted red ;
}
.address-list{
    width: 100%;
    overflow: hidden;
}
.address-list li {
    box-sizing: border-box;
    position: relative;
    width: 100%;
    background: #fff;
    margin-top:8px;
    height: 100px;
    overflow: hidden;
    
}
.address-list li p{
    text-align: left;
    margin-left:100px;
    letter-spacing: 4px;
}
.newa-ddress{
    width: 100%;
    position: absolute;
    top:50px;
}
.adr-pro{
    margin-top:10px;
    font-size: 20px;
    font-weight: bold;
}
.local-box{
    text-align: left;
    width: 100%;
    height: 100px;
    border-radius: 10px;
    margin-top:20px;
    background: rgb(247, 33, 33);
    position: relative;
    overflow: hidden;
    color: #fff;
    top:-10px;
    margin-bottom:140px;
    
}
.locbox{
    position: absolute;
    top:40px;
    width: 300px;
    left:120px;
}
.locbox p{
    padding-left:10px;
    float: left;
    margin-left:20px;
    
}
.local-title{
    position: absolute;
    bottom:50px;
    left: 20px;
    font-weight: bold;
    font-size: 24px;
}
.back{
    position: absolute;
    cursor: pointer;
    display: block;
    width: 100px;
    height: 30px;
    font-size: 18px;
    left:-30px;
    top:0;
    color: #fff;
}
.chose-adress {
    position: relative;
}
.chose-adress input{
    width: 200px;
    height: 40px;
    margin-top:10px;
    padding-left:10px;
    letter-spacing: 3px;
    position: absolute;
    left:50%;
    transform: translateX(-50%); 
    display: block;
    float: left;
}
.proves{
    top:20px;
}
.city{
    top:80px;
}
.road{
    top:140px;
}

.order-show{
    width: 100%;
}
.btn{

    border-radius: 10px;
    color:#fff;
    outline: none;
    border: none;
    line-height: 40px;
    font-size: 12px;
    width:70px;
    height: 70px;
    background: rgb(45, 63, 2);
    cursor: pointer;
    position: fixed;
    z-index: 100;
}
.order-local-btn{
    left:100px;
    bottom: 60px;
}

.order-sure-btn{
    left:0px;
    bottom:60px;
}

.order-newaddress-btn{
    bottom:100px;
    right:30px;
}
.order-choseaddress-btn{
    left:30px;
    bottom:100px;   
}
.order-show li{
    margin-top:10px;
    padding: 10px;
    display: block;
    box-sizing: border-box;
    height: 120px;
    background: #fff;
    position: relative;
    margin-top:10px;

}
.order-show p{
    text-align: left;
    margin-left:100px;
}
.name{

    white-space: nowrap;
    overflow: hidden;
    text-overflow:ellipsis;
    font-weight: bold;
    margin-top:10px;
}
.total{
    position: absolute;
    right:30px;
    color:red;
    bottom:20px;
    font-weight: bold;
    font-size: 18px;
}
.num{
    position: absolute;
    right:180px;
    color:rgb(161, 161, 161);
    bottom:20px;
    font-size: 18px;
}
.order-show .orderbookimg{
    width: 20%;
    position: absolute;
    left:10px;
    top:10%;
   
}
</style>



