
<template>

    <div class="page home">
               
      
        <!-- 轮播图 -->
        <swiper :options="swiperOption" ref="mySwiper" >
           
                <swiper-slide class="swpbox "><img class="shadowOne" :src=books[numbox[8]].cover  alt="">
                    <p class="title">{{books[numbox[8]].name}}</p>
                    <p class="price">${{books[numbox[8]].price}}</p></swiper-slide>
                <swiper-slide class="swpbox"><img class="shadowOne" :src=books[numbox[9]].cover  alt="">
                    <p class="title">{{books[numbox[9]].name}}</p>
                    <p class="price">${{books[numbox[9]].price}}</p></swiper-slide>
                <swiper-slide class="swpbox"><img class="shadowOne" :src=books[numbox[10]].cover  alt="">
                    <p class="title">{{books[numbox[10]].name}}</p>
                    <p class="price">${{books[numbox[10]].price}}</p></swiper-slide>
        </swiper>
        <div class="swiper-button-prev slide-change" slot="button-prev"></div>
         <div class="swiper-button-next slide-change" slot="button-next"></div>
     <main>
        
        <!-- 热卖商品 -->
        <p class="part-one part">热卖商品</p>
        <div @click="tobookspage" class="hot-goods">
            <div class="fl">
                <div class="best">
                   <img :src=books[numbox[0]].cover  alt="">
                    <p class="title">{{books[numbox[0]].name}}</p>
                    <p class="price">${{books[numbox[0]].price}}</p>

                </div>
                <div class="book"> <img :src=books[numbox[1]].cover  alt="">
                    <p class="title">{{books[numbox[1]].name}}</p>
                    <p class="price">${{books[numbox[1]].price}}</p></div>
            </div>
            <div class="fr">
                 <div class="book"> <img :src=books[numbox[2]].cover  alt="">
                    <p class="title">{{books[numbox[2]].name}}</p>
                    <p class="price">${{books[numbox[2]].price}}</p></div>
                  <div class="book"> <img :src=books[numbox[3]].cover  alt="">
                    <p class="title">{{books[numbox[3]].name}}</p>
                    <p class="price">${{books[numbox[3]].price}}</p></div>
                <div class="book"> <img :src=books[numbox[4]].cover  alt="">
                    <p class="title">{{books[numbox[4]].name}}</p>
                    <p class="price">${{books[numbox[4]].price}}</p></div>
            </div>

        </div>
        <!-- 新品推荐 -->
        <p class="part-two part">新品推荐</p>
        <ul @click="tobookspage"  class="recommend-goods">
            <li class="books-home-rec"><img :src=books[numbox[5]].cover alt=""><p>初级程序员推荐</p></li>
            <li class="books-home-rec"><img :src=books[numbox[6]].cover alt=""><p>中级程序员推荐</p></li>
            <li class="books-home-rec"><img :src=books[numbox[7]].cover alt=""><p>高级程序员推荐</p></li>
        </ul>
        </main>
    
    </div>

</template>

<script>
import 'swiper/dist/css/swiper.css'
import { swiper, swiperSlide } from 'vue-awesome-swiper'
 
export default {
    name: 'Home',
      components: {
        swiper,
        swiperSlide
         },
    data() {
        return {
            books:null,
            idx:null,
            numbox:[],
            swiperOption: {
                loop: true,
                pagination: ".swiper-pagination",
                navigation: {
                    nextEl: '.swiper-button-next',
                    prevEl: '.swiper-button-prev',
                },
                scrollbar: {
                    el: '.swiper-scrollbar',
                }
             }
         
      }
    },
    created() {
      //加载后台json文件
        this.$axios.get('https://www.easy-mock.com/mock/5c1b0deb48952b7bd6514342/example/lykmobileshopAppdata',{
    }).then(res=>{
        //加载图书数据
        this.books = res.data;
       localStorage.books = JSON.stringify(this.books)
    }).catch(error =>{
        console.log(error)
    }); 
        //随机生成展示物品的下标
        var a = [];
        for(var i = 0; i < 27; i++){
            a.push(i);
        }
        //打乱顺序排序
        a.sort(function(){return 0.5 - Math.random()});
        a.length = 11;
        this.numbox = a;
        var _this = this;
    
        //给予定时器规律跳转改变类名
        //呼吸灯
        setInterval(function(){
        this.showshadow = !this.showshadow;
        //toggleClass操控class切换
            _this.$('.swiper-container .swpbox').toggleClass('shadowOne')
        },1500);       
    },

    methods:{
        tobookspage(){
            this.$router.push({path:'../books',query:{title:'书城'}})
        },
        //判断是否为第一次使用该app
       
        },
    
  
   computed: {
        swiper() {
            return this.$refs.mySwiper.swiper
        }
    }
   
}
</script>

<style scoped>
.swpbox img{
    position: absolute;
    width: 40%;
    height: 100%;
    left:0;
    z-index: -20;
    display: block;
    
}
.swpbox .title{
    position:absolute;
    right:20px;
    top:40px;
    font-size: 20px;
    width: 200px;
    font-weight: bold;
}
.shadowOne{
    box-shadow: rgb(65, 41, 5)  0 0 40px inset;
}
.swpbox .price{
    position: absolute;
    bottom:40px;
    font-size: 40px;
    right:50px;
}
.swpbox{
    transition: 1s;
    border-radius: 30px;
}
.slide-change{
    position: absolute;
    top:14%;
    outline: none;
    width: 100px;
    height: 200px; 
    opacity: 0;
    cursor: auto;

}
.swiper-container {
    width: 100%;
    margin-top:74px;
    height: 200px;
    background: rgba(255, 255, 255, 0.507);
    position: relative;
    border-radius: 30px;
    z-index: 10;
    transition: .5s;
}
main{
    overflow: hidden;
}
.books-home-rec {
    width: 100%;
    height: 140px;
    background: #fff;
    margin-top:10px;
    overflow: hidden;
}
.books-home-rec p{
    width: 80%;
    margin-left:30px;
    font-weight: bold;
    font-size: 30px;
    margin-top:50px;

}
.recommend-goods{
    width: 100%;
    margin-bottom: 60px;

}
.recommend-goods .books-rec{
    width: 100%;
    background: #fff;
    height: 120px;
    margin-top:14px;
    text-align: left;

}

.books-home-rec img{
    width: 25%;
    height: 80%;
    margin:10px 0;
    float: left;
    
}
.search-btn-box{
    margin:0 auto;
    margin-top:10px;
    width: 96%;
    padding: 3px;
    height: 30px;
    border-radius: 15px;
    outline: none;
    position: relative;
    border:none;
}
.search-btn-box input{
    border: none;
    outline: none;
    position: absolute;
    left:30px;
    top: 50%;
    transform: translateY(-50%);
    height: 28px;
    line-height: 28px;
    background: none;
}
.show-img{
    margin-top:10px;
    width: 100%;}
.show-img img{
    width: 100%;
    height: 200px;
}
.hot-goods{
    width: 100%;
    height: auto;
    text-align: left;
}
.booksimg{
    width: 30%;
    margin-top:10px;
}
.price{
    color:red;
    font-weight: bold;
}
.title{
    font-size: 14px;
    width: 110px;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    
}
.part{
    font-weight: bold;
    font-size: 16px;
}
.part-one{
    margin-top:10px;
    margin-bottom: 20px;
}
.part-two{
    margin-top:365px;
    margin-bottom: 20px;

}
.fr,.fl{
    float: left;
    width: 48%;
    height: 328px;


}
.book{
    box-sizing: border-box;
    padding: 4px;
    width: 100%;
    height: 100px;
    background: #fff;
    margin-bottom: 14px;
}

.fl .book img{
    float: left;
    width: 50px;
    height:70px;

    margin-top:8px;
}
.fl .best{
    box-sizing: border-box;
    padding: 4px;
    width: 100%;
    background: #fff;
    margin-bottom: 14px;
    height: 214px;
}
.fl .best img{

    display: block;
    margin:0 auto 20px;
    width: 110px;
    height: 130px;
}
.fl .book p{
      float: left;
    margin-left:4px;
    margin-top:10px;
}
.fr{
    margin-left:4%;
}
.fr .book img{
    float: left;
    width: 50px;
    height:70px;

    margin-top:8px;
}
.fr p {
    float: left;
    margin-left:4px;
    margin-top:10px;
}

</style>


